import React                                        from 'react';
import PropTypes                                    from 'prop-types';

/* ################################################### */
/*          ui framework         */
/* ################################################### */

import RaisedButton                                 from 'material-ui/RaisedButton';
import TextField                                    from 'material-ui/TextField';
import moment                                       from 'moment';

/* ################################################### */
/*          abstract         */
/* ################################################### */
import ComponentAbstract                            from '../../_abstract/c.abstract';

import '../s.broadcast.message.css';

export default class ComponentBroadcastMessagePreview extends ComponentAbstract {
    static propTypes = {
        form        : PropTypes.object
        , moduleSelection : PropTypes.array
        , modeSelection : PropTypes.array

        , onClickConfirm           : PropTypes.func
        , onClickCancelConfirm       : PropTypes.func
    }

    static defaultProps = {
        form : {}
        , moduleSelection : []
        , modeSelection : []

        , onClickConfirm            : () => {}
        , onClickCancelConfirm        : () => {}
    }

    constructor( props ) {
        super( props )
    }

     /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */
   render() {
        return (
            <div>
                    <div
                        className={ ' broadcast-message-marginBottom-10 ' }
                            style={ { fontWeight : 'bolder' } }>
                        {
                            ( ' Broadcast Messages Management  ' )
                        }
                    </div>
                    <br/>
                    <div style={ { paddingLeft : '20px' } } >
                            <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                                <div className={ ' broadcast-message-flex-10 ' }>
                                    Title
                                </div>

                                <div className={ ' broadcast-message-flex-60 ' }>
                                    { this.props.form['title'] }
                                </div>
                            </div>

                            <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                                <div className={ '  broadcast-message-flex-10 ' }>
                                    Message
                                </div>

                                <div className={ ' broadcast-message-flex-60 ' }>
                                    <TextField 
                                                name={ 'input_message' }
                                                underlineStyle={ { borderColor: '#000' } }
                                                underlineFocusStyle={ { borderColor: '#FF5252' } }
                                                style={ { width : '50%' } }
                                                multiLine={true}
                                                max={10}
                                                rows={10}
                                                rowsMax={10}
                                                value={ this.props.form['message'] }
                                            />
                                </div>
                            </div>

                            <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                                <div className={ ' broadcast-message-flex-10 ' }>
                                    Start Date
                                </div>

                                <div className={ ' broadcast-message-flex-60 ' }>
                                    { moment(this.props.form['formattedStartDate'].substr(0, 8)+'T'+this.props.form['formattedStartDate'].substr(8, 6)).format('YYYY-MM-DD HH:mm:ss') }
                                </div>
                            </div>

                            <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                                <div className={ ' broadcast-message-flex-10 ' }>
                                    End Date
                                </div>

                                <div className={ ' broadcast-message-flex-60 ' }>
                                { moment(this.props.form['formattedEndDate'].substr(0, 8)+'T'+this.props.form['formattedEndDate'].substr(8, 6)).format('YYYY-MM-DD HH:mm:ss') }
                                </div>
                            </div>

                            <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                                <div className={ ' broadcast-message-flex-10 ' }>
                                    Module
                                </div>

                                <div className={ ' broadcast-message-flex-60 ' }>
                                    { this.props.moduleSelection.find((obj) => {return obj.id === (+this.props.form.module)}).name }
                                </div>
                            </div>

                            <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                                <div className={ ' broadcast-message-flex-10 ' }>
                                    Mode
                                </div>

                                <div className={ ' broadcast-message-flex-60 ' }>
                                { this.props.modeSelection.find((obj) => {return obj.id === (+this.props.form.mode)}).name }
                                </div>
                            </div>
                    </div>
                 
                    <div>
                        
                        <RaisedButton
                        className={ 'broadcast-message-margin-10' }
                        label={ "Back" }
                        onClick={ () => {
                            this.props.onClickCancelConfirm();
                        } } />
                                                    
                        <RaisedButton
                                    className={ 'broadcast-message-margin-10' }
                                    label={ "Confirm" }
                                    onClick={ () => {
                                        this.props.onClickConfirm();
                                    } } /> 

                    </div>

            </div>
        );
    }
}


