import React                                        from 'react';
import {
    connect
}                                                   from 'react-redux';

/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Step,
    Stepper,
    StepLabel,
  }  
                                                   from 'material-ui/Stepper';
import moment                                       from 'moment';

/* ################################################### */
/*          constant         */
/* ################################################### */

import { AppConstant }                              from '../../../_constant/constant.app';
import { ServiceEnumHTTP }                          from '../../../_service/helper/service.enum.http';

/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionBroadcastMessageDetail , { ReduxEnumBroadcastMessageDetail } from '../../../_redux/action/reduxAction.broadcast.message.detail';

/* ################################################### */
/*          page         */
/* ################################################### */

import ComponentAbstract                            from '../../_abstract/c.abstract';

/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomLoading                       from '../../_component/loading/cc.loading';
import ComponentBroadcastMessageInput               from './c.broadcast.message.input';
import ComponentBroadcastMessagePreview             from './c.broadcast.message.preview';
import ComponentBroadcastMessageAcknowledgement             from './c.broadcast.message.acknowledgement';

/* ################################################### */
/* 		 util		 */
/* ################################################### */

import HelperNavigate , { HelperNavigateENUM }		from '../../_helper/h.navigate';
import HelperStorage		                        from '../../_helper/h.storage';

import { StringUtil }                               from '../../../_util/util.string';
import FormValidateUtil                             from '../../../_util/util.formValidate';

import '../s.broadcast.message.css';

export default connect(
    ( state ) => {
        let thisState = state[ 'ReduxBroadcastMessageDetail' ] ;
            let stateObj = { };

            if ( thisState ) {

                for ( let i = 0 ; i < thisState.length; i ++ ) {
                    let recordItem = thisState[ i ];
                    if ( !recordItem[ 'completed' ] ) {


                        if ( ReduxEnumBroadcastMessageDetail.INIT_DONE === recordItem[ 'type' ] ) {
                            // stateObj[ 'onReadyFunction' ]       = 'requestInitDone';
                            // stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumBroadcastMessageDetail.LIST_DETAIL_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestListDetailDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];
                        } else if ( ReduxEnumBroadcastMessageDetail.SUBMIT_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestSubmitDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumBroadcastMessageDetail.CONFIRM_SUBMIT_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestConfirmSubmitDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumBroadcastMessageDetail.DELETE_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestDeleteDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumBroadcastMessageDetail.CONFIRM_DELETE_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestConfirmDeleteDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumBroadcastMessageDetail.UPDATE_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestUpdateDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumBroadcastMessageDetail.CONFIRM_UPDATE_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestConfirmUpdateDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        }

                        recordItem[ 'completed' ] = true;
                    }
                }

            }

            return stateObj;
    }) (
        class ComponentBroadcastMessageDetail extends ComponentAbstract {

            constructor( props ) {
                super( props );

                console.log( 'ComponentBroadcastMessageDetail : constructor : *** : [' + this.uuid() + '] ' , props );

                this.state = {
                    step : 0
                    
                    , isLoading : false
                    
                    , result : {}

                    , form : {
                        bmId: '',
                        title: '',
                        message: '',
                        startDate: '',
                        formattedStartDate: '',
                        endDate: '',
                        formattedEndDate: '',
                        module: '',
                        mode: ''
                    }

                    ,MODULE : props['location']['state']['module']
                    , MODE : props['location']['state']['mode']
                    , status : props['location']['state']['status']
                    , statusHeader: props['location']['state']['statusHeader']

                    , formValidation : {}
                }
            }

            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        listener
                ################################################################################################################################################
                ################################################################################################################################################
            */
           _onReady() {
                
                if (this.state.status === 'view') {  
                    this.state.form = this.props['location']['state']['item']; 
                    this.requestListDetail();
                }
            }

            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        event
                ################################################################################################################################################
                ################################################################################################################################################
            */
           _handleChange( key , value ) {
                let form = this.state.form;
                form[ key ] = value;
                this.setState( {
                    form : form
                } )
            }

           onClickCancel( ) {
                console.log( 'ComponentBroadcastMessageDetail : onClickCancel : *** ' );

                HelperNavigate.route(
                    HelperNavigate.mapPath( HelperNavigateENUM.BROADCAST_MESSAGE_LIST )
                );
            }

            onClickCancelConfirm( ) {
                console.log( 'ComponentBroadcastMessageDetail : onClickCancelConfirm : *** ' );
                this.requestStep(0);
            }

            requestStep( step, status, statusHeader ) {
                this.setState( {
                    step     : step
                    , status : ( status ) ? status : this.state.status
                    , statusHeader : ( statusHeader ) ? statusHeader : this.state.statusHeader
                } );
            }

            onClickEdit() {
                console.log( 'ComponentBroadcastMessageDetail : onClickEdit : *** ' );
                this.requestStep(0, 'edit', 'Edit Broadcast Message');
            }

            onClickDelete() {
                console.log( 'ComponentBroadcastMessageDetail : onClickDelete : *** ' );
                this.requestDelete();
            }

            onClickSubmit() {
                console.log( 'ComponentBroadcastMessageDetail : onClickSubmit : *** ' );
                FormValidateUtil.validate(
                    this.state.form
                    , {
                        title   : { required : true }
                        , message : { required : true }
                        , startDate : { required : true }
                        , endDate : { required : true }
                        , module : { required : true }
                        , mode : { required : true }
                    }
                ).then(
                    ( value ) => {
                        if (this.state.status === 'add') {
                            this.requestSubmit();
                        } else if (this.state.status === 'edit') {
                            this.requestUpdate();
                        }
                    }
                ).catch(
                    ( error ) => {
                        this.setState( { formValidation : error } );
                    }
                );
            }

            onClickUpdate() {
                console.log( 'ComponentBroadcastMessageDetail : onClickUpdate : *** ' );
                this.onClickSubmit();
            }

            onClickConfirm( ) {
                console.log( 'ComponentBroadcastMessageDetail : onClickConfirm : *** ' );
                if (this.state.status === 'add') {
                    this.requestConfirmSubmit();
                } else if (this.state.status === 'edit') {
                    this.requestConfirmUpdate();
                } else if (this.state.status === 'delete') {
                    this.requestConfirmDelete();
                }
            }
            

             /*
        		################################################################################################################################################
        		################################################################################################################################################
        				request
        		################################################################################################################################################
        		################################################################################################################################################
            */

           requestListDetail( ) {
                console.log( 'ComponentBroadcastMessageDetail : requestListDetail : ***' )

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading: true }
                            , () => {
                                this.dispatch(
                                    ReduxActionBroadcastMessageDetail.rListDetail(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , { bmId : this.state.form.bmId }
                                    )
                                )
                            }
                        );

                    }
                )

            }

            requestListDetailDone( result ) {
                console.log( 'ComponentBroadcastMessageDetail : requestListDetailDone : *** : result => ', result );

                this.setState( {
                    isLoading: false
                    , result : result
                } );

                this._handleChange('bmId', result['bmId']);
                this._handleChange('title', result['title']);
                this._handleChange('message', result['message']);
                this._handleChange('startDate', new Date(moment(result['startDate'].substr(0, 8)).format('YYYY-MM-DD')));
                this._handleChange('formattedStartDate', result['startDate']);
                this._handleChange('endDate', new Date(moment(result['endDate'].substr(0, 8)).format('YYYY-MM-DD')));
                this._handleChange('formattedEndDate', result['endDate']);
                this._handleChange('module', result['module']);
                this._handleChange('mode', result['mode']);
                               
                this.dispatch(
                    ReduxActionBroadcastMessageDetail.rRequestEnd()
                );
            }
            requestSubmit() {
                console.log( 'ComponentBroadcastMessageDetail : requestSubmit : *** ' );

                
                        Promise.all( [
                            ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                            , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                        ] ).then(
                            ( value ) => {
        
                                this.setState(
                                    { isLoading : true }
                                    , () => {
                                        this.dispatch(
                                            ReduxActionBroadcastMessageDetail.rSubmit(
                                                { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                , {
                                                    title: this.state.form.title,
                                                    message: this.state.form.message,
                                                    startDate: this.state.form.formattedStartDate,
                                                    endDate: this.state.form.formattedEndDate,
                                                    module: this.state.form.module,
                                                    mode: this.state.form.mode 
                                                }
                                            )
                                        )
                                    }
                                );
        
                            }
                        );
            }
            requestSubmitDone( result ) {
                console.log( 'ComponentBroadcastMessageDetail : requestSubmitDone : *** : result => ' , result );

                this.setState( {
                    isLoading: false
                    , result : result
                } );
                this.dispatch(
                    ReduxActionBroadcastMessageDetail.rRequestEnd()
                );
            
                this.requestStep(1);
            }

            requestConfirmSubmit() {
                console.log( 'ComponentBroadcastMessageDetail : requestConfirmSubmit : *** ');

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionBroadcastMessageDetail.rConfirmSubmit(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] },
                                        { instructionId : this.state.result['instructionId'] }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestConfirmSubmitDone( result ) {
                console.log( 'ComponentBroadcastMessageDetail : requestConfirmSubmitDone : *** : result => ' , result );

                this.setState( {
                    isLoading: false
                    , result : result
                } );
                this.dispatch(
                    ReduxActionBroadcastMessageDetail.rRequestEnd()
                );

                this.requestStep(2);
            }
            requestUpdate() {
                console.log( 'ComponentBroadcastMessageDetail : requestUpdate : *** ' );

                
                        Promise.all( [
                            ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                            , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                        ] ).then(
                            ( value ) => {
        
                                this.setState(
                                    { isLoading : true }
                                    , () => {
                                        this.dispatch(
                                            ReduxActionBroadcastMessageDetail.rUpdate(
                                                { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                , {
                                                    bmId: this.state.form.bmId,
                                                    title: this.state.form.title,
                                                    message: this.state.form.message,
                                                    startDate: this.state.form.formattedStartDate,
                                                    endDate: this.state.form.formattedEndDate,
                                                    module: this.state.form.module,
                                                    mode: this.state.form.mode 
                                                }
                                            )
                                        )
                                    }
                                );
        
                            }
                        );
            }
            requestUpdateDone( result ) {
                console.log( 'ComponentBroadcastMessageDetail : requestUpdateDone : *** : result => ' , result );

                this.setState( {
                    isLoading: false
                    , result : result
                } );
                this.dispatch(
                    ReduxActionBroadcastMessageDetail.rRequestEnd()
                );
            
                this.requestStep(1);
            }

            requestConfirmUpdate() {
                console.log( 'ComponentBroadcastMessageDetail : requestConfirmUpdate : *** ');

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionBroadcastMessageDetail.rConfirmUpdate(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] },
                                        { instructionId : this.state.result['instructionId'] }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestConfirmUpdateDone( result ) {
                console.log( 'ComponentBroadcastMessageDetail : requestConfirmUpdateDone : *** : result => ' , result );

                this.setState( {
                    isLoading: false
                    , result : result
                } );
                this.dispatch(
                    ReduxActionBroadcastMessageDetail.rRequestEnd()
                );

                this.requestStep(2);
            }

            requestDelete() {
                console.log( 'ComponentBroadcastMessageDetail : requestDelete : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionBroadcastMessageDetail.rDelete(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , { bmId : this.state.form.bmId }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestDeleteDone( result ) {
                console.log( 'ComponentBroadcastMessageDetail : requestDeleteDone : *** : result => ' , result );

                this.setState( {
                    isLoading: false
                    , result : result
                } );
                this.dispatch(
                    ReduxActionBroadcastMessageDetail.rRequestEnd()
                );

                this.requestStep(1, 'delete', 'Delete Broadcast Message');
            }

            requestConfirmDelete() {
                console.log( 'ComponentBroadcastMessageDetail : requestConfirmDelete : *** ');

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionBroadcastMessageDetail.rConfirmDelete(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] },
                                        { instructionId : this.state.result['instructionId'] }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestConfirmDeleteDone( result ) {
                console.log( 'ComponentBroadcastMessageDetail : requestConfirmDeleteDone : *** : result => ' , result );

                this.setState( {
                    isLoading: false
                    , result : result
                } );
                this.dispatch(
                    ReduxActionBroadcastMessageDetail.rRequestEnd()
                );

                this.requestStep(2);
            }
             /*
        		################################################################################################################################################
        		################################################################################################################################################
        				render
        		################################################################################################################################################
        		################################################################################################################################################
            */
            render() {
                return (

                   <div>                       

                        <div className={ ' broadcast-message-detail-padding-20 ' } ></div>

                        <div
                                className={ ' broadcast-message-detail-margin-auto broadcast-message-detail-paddingTop-20 broadcast-message-detail-paddingBottom-20  broadcast-message-detail-maxWidth-900 ' }>

                            <Stepper activeStep={ this.state.step }>

                                <Step>
                                    <StepLabel>
                                        { this.state.statusHeader }
                                    </StepLabel>
                                </Step>

                                <Step>
                                    <StepLabel> Preview </StepLabel>
                                </Step>

                                <Step>
                                    <StepLabel> Acknowledgement </StepLabel>
                                </Step>

                            </Stepper>

                        </div>


                        <div className={ ' broadcast-message-detail-divider ' }></div>


                       
                        <div>

                            {
                                ( () => {

                                    switch ( this.state.step ) {

                                        case 0:
                                            return (
                                                <ComponentBroadcastMessageInput
                                                            form={ this.state.form }
                                                            formValidation={ this.state.formValidation }
                                                            status={ this.state.status }
                                                            moduleSelection={ this.state.MODULE }
                                                            modeSelection={ this.state.MODE }

                                                            onChangeForm={ ( key , value ) => {
                                                                console.log( 'ComponentBroadcastMessageInput : change form value => ' , value );

                                                                let form = this.state.form;
                                                                form[ key ] = value;
                                                                this.setState( { form : form } );
                                                            } }
                                                            onClickEdit={ () => {
                                                                this.onClickEdit();
                                                            } }
                                                            onClickDelete={ () => {
                                                                this.onClickDelete();
                                                            } }
                                                            onClickSubmit={ () => {
                                                                this.onClickSubmit();
                                                            } }
                                                            onClickUpdate={ () => {
                                                                this.onClickUpdate();
                                                            } }
                                                            onClickCancel={ () => {
                                                                this.onClickCancel();
                                                            } }
                                                />
                                            );
                                            break;

                                        case 1:
                                        return (
                                            <ComponentBroadcastMessagePreview
                                                    form={ this.state.form }
                                                    moduleSelection={ this.state.MODULE }
                                                    modeSelection={ this.state.MODE }

                                                    onClickConfirm={ () => {
                                                        this.onClickConfirm();
                                                    } }
                                                    onClickCancelConfirm={ 
                                                        () => {
                                                            this.onClickCancelConfirm();
                                                        }
                                                    }
                                        />
                                        );
                                            break;

                                        case 2:
                                            
                                        return (
                                            <ComponentBroadcastMessageAcknowledgement 
                                                status={ this.state.status }
                                            />
                                        );

                                            break;

                                        default:
                                            return null;

                                    }

                                } )()
                            }

                        </div>
                         

                        <ComponentCustomLoading
                                        visible={ this.state.isLoading }
                        />

                    </div>

                    
                );
            }
        }
    )


