import React                                        from 'react';
import PropTypes                                    from 'prop-types';

/* ################################################### */
/*          ui framework         */
/* ################################################### */

import RaisedButton                                 from 'material-ui/RaisedButton';
import TextField                                    from 'material-ui/TextField';
import DatePicker                                   from 'material-ui/DatePicker';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import moment                                       from 'moment';

/* ################################################### */
/*          helper         */
/* ################################################### */
import HelperNavigate , { HelperNavigateENUM }		from '../../_helper/h.navigate';
import HelperStorage		                        from '../../_helper/h.storage';

/* ################################################### */
/*          abstract         */
/* ################################################### */
import ComponentAbstract                            from '../../_abstract/c.abstract';

import '../s.broadcast.message.css';

export default class ComponentBroadcastMessageInput extends ComponentAbstract {
    
    static propTypes = {
        form        : PropTypes.object
        , formValidation    : PropTypes.object
        , status : PropTypes.string
        , moduleSelection : PropTypes.array
        , modeSelection : PropTypes.array

        , onChangeForm      : PropTypes.func

        , onClickSubmit       : PropTypes.func
        , onClickUpdate       : PropTypes.func
        , onClickCancel       : PropTypes.func

    }

    static defaultProps = {
        form : {}
        , formValidation    : {}
        , moduleSelection : []
        , modeSelection : []

        , onChangeForm      : () => {}

        , onClickSubmit       : () => {}
        , onClickUpdate        : () => {}
        , onClickCancel        : () => {}
    
    }

    constructor( props ) {
        super( props )
    }

     /*
        ################################################################################################################################################
        ################################################################################################################################################
                event
        ################################################################################################################################################
        ################################################################################################################################################
    */

   getStartDate() {
    if (this.props.form.startDate) {
        return new Date(this.props.form.startDate);
        }
    }

    getEndDate() {
        if (this.props.form.endDate) {
            return new Date(this.props.form.endDate);
        }
    }

    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */
   render() {
    return (

            (this.props.status === 'view') 
            ?
            <div>
                        
                <div style={ { paddingLeft : '20px' } } >
                    <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                        <div className={ ' broadcast-message-flex-10 ' }>
                            Title
                        </div>

                        <div className={ ' broadcast-message-flex-60 ' }>
                            { this.props.form['title'] }
                        </div>
                    </div>

                    <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                        <div className={ ' broadcast-message-flex-10 ' }>
                            Message
                        </div>

                        <div className={ ' broadcast-message-flex-60 ' }>
                            <TextField 
                                            name={ 'input_message' }
                                            style={ { width : '50%' } }
                                            multiLine={true}
                                            max={10}
                                            rows={10}
                                            rowsMax={10}
                                            value={ this.props.form['message'] }
                                            />
                        </div>
                    </div>

                    <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                        <div className={ ' broadcast-message-flex-10 ' }>
                            From Date/ Time
                        </div>

                        <div className={ ' broadcast-message-flex-60 ' }>
                            { (this.props.form['formattedStartDate']) ? moment(this.props.form['formattedStartDate'].substr(0, 8)+'T'+this.props.form['formattedStartDate'].substr(8, 6)).format('YYYY-MM-DD HH:mm:ss') : '' }
                        </div>
                    </div>

                    <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                        <div className={ ' broadcast-message-flex-10 ' }>
                            To Date/ Time
                        </div>

                        <div className={ ' broadcast-message-flex-60 ' }>
                            { (this.props.form['formattedEndDate']) ? moment(this.props.form['formattedEndDate'].substr(0, 8)+'T'+this.props.form['formattedEndDate'].substr(8, 6)).format('YYYY-MM-DD HH:mm:ss') : '' }
                        </div>
                    </div>  

                    <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                        <div className={ ' broadcast-message-flex-10 ' }>
                        Module
                        </div>

                        <div className={ ' broadcast-message-flex-60 ' }>
                            {  (this.props.form['module']) ? this.props.moduleSelection.find((obj) => obj.id === (+this.props.form['module'])).name : '' }
                        </div>
                    </div> 

                    <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                        <div className={ ' broadcast-message-flex-10 ' }>
                        Mode
                        </div>

                        <div className={ ' broadcast-message-flex-60 ' }>
                        {  (this.props.form['mode']) ? this.props.modeSelection.find((obj) => obj.id === (+this.props.form['mode'])).name : '' }
                        </div>
                    </div>

                    <div>
                
                        <RaisedButton
                        className={ 'broadcast-message-margin-10' }
                        label={ "Back" }
                        onClick={ () => {
                            this.props.onClickCancel();
                        } } />
                                            
                        <RaisedButton
                                    className={ 'broadcast-message-margin-10' }
                                    label={ "Edit" }
                                    onClick={ () => {
                                        this.props.onClickEdit();
                                    } } /> 

                        <RaisedButton
                                    className={ 'broadcast-message-margin-10' }
                                    label={ "Delete" }
                                    onClick={ () => {
                                        this.props.onClickDelete();
                                    } } /> 

                    </div>
                </div>

            </div>
            :

            <div>

                                
                    <div style={ { paddingLeft : '20px' } } >

                        <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                            <div className={ ' broadcast-message-flex-10 ' }>
                                Title
                            </div>

                            <div className={ ' broadcast-message-flex-60 ' }>
                                <TextField 
                                                name={ 'input_title' }
                                                underlineStyle={ { borderColor: '#000' } }
                                                underlineFocusStyle={ { borderColor: '#FF5252' } }
                                                style={ { width : '50%' } }
                                                errorText={ this.props.formValidation[ 'title' ] }
                                                value={ this.props.form['title'] }
                                                onChange={ ( event, value ) => {
                                                    this.props.onChangeForm( 'title' , value );
                                                } } />
                            </div>
                        </div>

                        <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                            <div className={ ' broadcast-message-flex-10 ' }>
                                Message
                            </div>

                            <div className={ ' broadcast-message-flex-60 ' }>
                                <TextField 
                                                name={ 'input_message' }
                                                underlineStyle={ { borderColor: '#000' } }
                                                underlineFocusStyle={ { borderColor: '#FF5252' } }
                                                style={ { width : '50%' } }
                                                multiLine={true}
                                                max={10}
                                                rows={10}
                                                rowsMax={10}
                                                errorText={ this.props.formValidation[ 'message' ] }
                                                value={ this.props.form['message'] }
                                                onChange={ ( event, value ) => {
                                                    this.props.onChangeForm( 'message' , value );
                                                } } />
                            </div>
                        </div>

                        <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                            <div className={ ' broadcast-message-flex-10 ' }>
                                Start Date
                            </div>
                            <div className={ ' broadcast-message-flex-60 ' }>
                                <DatePicker
                                        name={ 'select_startDate' }
                                        underlineStyle={ { borderColor: '#000' } }
                                        underlineFocusStyle={ { borderColor: '#FF5252' } }
                                        style={ { flex: 1 } }
                                        hintText="Select Start Date" 
                                        errorText={ this.props.formValidation[ 'startDate' ] }
                                        value={ this.getStartDate() }
                                        onChange={ ( event, date ) => {
                                            this.props.onChangeForm( 'startDate' , date );
                                            this.props.onChangeForm( 'formattedStartDate' , (moment(date).format('YYYYMMDD') + moment().format('HHmmssSSS')) );
                                        } } /> 
                            </div>
                        </div>

                        <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                            <div className={ ' broadcast-message-flex-10 ' }>
                                End Date
                            </div>
                            <div className={ ' broadcast-message-flex-60 ' }>
                                <DatePicker
                                        name={ 'select_endDate' }
                                        underlineStyle={ { borderColor: '#000' } }
                                        underlineFocusStyle={ { borderColor: '#FF5252' } }
                                        style={ { flex: 1 } }
                                        hintText="Select End Date" 
                                        errorText={ this.props.formValidation[ 'endDate' ] }
                                        value={ this.getEndDate() }
                                        onChange={ ( event, date ) => {
                                            this.props.onChangeForm( 'endDate' , date );
                                            this.props.onChangeForm( 'formattedEndDate' , (moment(date).format('YYYYMMDD') + moment().format('HHmmssSSS')) );
                                        } } /> 
                            </div>
                        </div>

                        <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                            <div className={ ' broadcast-message-flex-10 ' }>
                                Module
                            </div>
                            <div className={ ' broadcast-message-flex-60 ' }>
                                <SelectField 
                                        style={{ flex: 1 }}
                                        name={ 'select_module' }
                                        underlineStyle={ { borderColor: '#000' } }
                                        underlineFocusStyle={ { borderColor: '#FF5252' } }
                                        floatingLabelText="Select Module" 
                                        errorText={ this.props.formValidation[ 'module' ] }
                                        value = { (+this.props.form['module']) }
                                        onChange={(event, index, value) => {
                                            this.props.onChangeForm('module', value);
                                        }}>
                                        <MenuItem value={ this.props.moduleSelection[0]['id'] } primaryText={ this.props.moduleSelection[0]['name'] } />
                                        <MenuItem value={ this.props.moduleSelection[1]['id'] } primaryText={ this.props.moduleSelection[1]['name'] } />
                                        <MenuItem value={ this.props.moduleSelection[2]['id'] } primaryText={ this.props.moduleSelection[2]['name'] } />
                                    </SelectField>
                            </div>
                        </div>

                        <div className={ ' broadcast-message-flex  broadcast-message-marginBottom-10 ' }>
                            <div className={ ' broadcast-message-flex-10 ' }>
                                Mode
                            </div>
                            <div className={ ' broadcast-message-flex-60 ' }>
                                <SelectField 
                                        style={{ flex: 1 }}
                                        name={ 'select_mode' }
                                        underlineStyle={ { borderColor: '#000' } }
                                        underlineFocusStyle={ { borderColor: '#FF5252' } }
                                        floatingLabelText="Select Mode" 
                                        errorText={ this.props.formValidation[ 'mode' ] }
                                        value = { (+this.props.form['mode']) }
                                        onChange={(event, index, value) => {
                                            this.props.onChangeForm('mode', value);
                                        }}>
                                        <MenuItem value={ this.props.modeSelection[0]['id'] } primaryText={ this.props.modeSelection[0]['name'] } />
                                        <MenuItem value={ this.props.modeSelection[1]['id'] } primaryText={ this.props.modeSelection[1]['name'] } />
                                    </SelectField>
                            </div>
                        </div>

                    </div>


                    <div>

                        <RaisedButton
                                            className={ 'error-message-margin-10' }
                                            label={ "Cancel" }
                                            onClick={ () => {
                                                this.props.onClickCancel();
                                            } } />

                        { 
                            (this.props.status === 'add') ?

                        <RaisedButton
                                    className={ 'error-message-margin-10' }
                                    label={ "Submit" }
                                    onClick={ () => {
                                        this.props.onClickSubmit();
                                    } } /> 
                            :
                        <RaisedButton
                                    className={ 'error-message-margin-10' }
                                    label={ "Update" }
                                    onClick={ () => {
                                        this.props.onClickUpdate();
                                    } } /> 
                        }

                    </div>

            </div>

        );
   }
}


