import React                                        from 'react';
import PropTypes                                    from 'prop-types';

/* ################################################### */
/*          ui framework         */
/* ################################################### */


/* ################################################### */
/*          abstract         */
/* ################################################### */
import ComponentAbstract                            from '../../_abstract/c.abstract';

import '../s.broadcast.message.css';

export default class ComponentBroadcastMessageAcknowledgement extends ComponentAbstract {
    static propTypes = {
        status : PropTypes.string
    }

    static defaultProps = {
        status : ''
    }

    constructor( props ) {
        super( props )
    }

     /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */
   render() {
        return (
            <div>
                    <div
                        className={ ' broadcast-message-marginBottom-10 ' }
                            style={ { fontWeight : 'bolder' } }>
                        {
                            (this.props.status === 'add') 
                            ? 
                            ('Broadcast message is successfully submitted')
                            :
                            (this.props.status === 'edit') 
                            ?
                            ('Broadcast message is successfully updated')
                            :
                            ('Broadcast message is successfully removed')
                        }
                    </div>

            </div>
        );
    }
}


